.NavImg1 {
  width: 70px;
  height: 50px;
  background: none;
  margin-right: 25px;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left {
  display: flex;
  align-items: center;
}
.title {
  color: teal;
  font-size: 42px;
  user-select: none;
  background: none;
  transition: color 0.3s ease;
}
.title:hover {
  color: plum;
}
.Navbar {
  background: transparent;
  border: 5px solid teal;
  display: flex;
  justify-content: space-between;
  width: 1000px;
  min-width: 460px;
  height: 100px;
  margin-top: 2%;
  align-items: center;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 25px;
  transition: border-color 0.3s ease;
}
.Navbar:hover {
  border: 5px solid plum;
}
.ButAndDrop {
  display: flex;
  align-items: center;
  justify-content: center;
}
.CartLenght {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 20px;
  border-bottom: 2px solid teal;
  margin-top: 2px;
  padding-bottom: 5px;
  font-size: 17px;
  color: teal;
}
.totalPrice {
  padding-right: 20px;
  font-size: 18px;
  color: teal;
  transition: color 0.3s ease;
}
.totalPrice:hover {
  color: plum;
}

@media (max-width: 450px) {
  .Navbar {
    height: 80px;
    min-width: 310px;
    padding-right: 12px;
    padding-left: 12px;
    display: flex;
    justify-content: space-between;
    margin: 0;
  }
  .NavImg1 {
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }
  .title {
    font-size: 22px;
  }
  .CartLenght {
    font-size: 16px;
  }
  .totalPrice {
    padding: 20px;
    font-size: 15px;
  }
  .main {
    justify-content: center;
  }
}
