.main {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 260px;
  background-color: white;
  border-radius: 15%;
  border: 4px solid teal;
  text-decoration: none;
  transition: border-color 0.3s ease;
}

.main:hover {
  border-color: plum;
}

.CardImg {
  width: 100%;
  height: 185px;
  border-radius: 25%;
}
.title {
  word-wrap: break-word;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(56, 56, 56);
}
.price {
  color: rgb(56, 56, 56);
  width: auto;
  margin-top: 8px;
  margin-left: 25px;
}
.form {
  display: flex;
  align-items: center;
  width: 200px;
  height: 30px;
}
.start {
  display: flex;
  justify-content: space-around;
}
.prevPrice {
  color: teal;
  text-decoration: line-through;
  text-decoration-color: black;
  opacity: 0.8;
}
.button {
  margin-top: 10px;
  margin-left: 10px;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 450px) {
  .start {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main {
    margin-top: 20px;
  }
  .button {
    margin: 0;
    padding-top: 5px;
  }
}
