.pointer {
  transform: rotate(1deg);
  width: 200px;
  height: 200px;
}
.text {
  color: rgb(136, 34, 136);
  transition: color 0.3s ease;
}
.text:hover {
  color: plum;
}
.PWTComponent {
  width: 500px;
  height: 150px;
  display: flex;
  font-size: 20px;
  justify-content: center;
  align-items: center;
  margin-left: 50%;
  user-select: none;
}

@media (max-width: 900px) {
  .PWTComponent {
    width: 200px;
    height: 200px;
    margin-left: 50%;
  }
}
@media (max-width: 417px) {
  .PWTComponent {
    width: auto;
    height: 150px;
    margin-left: 5%;
  }
  .pointer {
    width: 150px;
    height: 150px;
  }
}
