.button {
  width: 100%;
  max-width: 93px;
  height: 20px;
  border-bottom: 2px solid teal;
  border-top: none;
  border-left: none;
  border-right: 4px solid teal;
  border-bottom-right-radius: 20px;
  background: none;
  padding-right: 8px;
  padding-bottom: 20px;
  cursor: pointer;
  font-size: 11px;
  transition: border-color 0.3s ease;
}
.button:hover {
  border-bottom: 2px solid plum;
  border-top: none;
  border-left: none;
  border-right: 4px solid plum;
}
@media (max-width: 450px) {
  .button {
    max-width: 60px;
    border-right: none;
    border-radius: 0;
    font-size: 12px;
    height: 22px;
    padding: 0;
  }
  .button:active {
    border-right: none;
  }
}
