.start {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  align-items: center;
}
.main {
  display: flex;
  flex-direction: column;
  width: 200px;
  height: 260px;
  background-color: white;
  border-radius: 15%;
  margin: 50px;
  border: 4px solid teal;
  text-decoration: none;
  transition: border-color 0.3s ease;
}
.main:hover {
  border: 4px solid plum;
}
.CardImg {
  width: 100%;
  height: 185px;
  border-radius: 25%;
}
.title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(56, 56, 56);
}
.price {
  color: rgb(56, 56, 56);
  width: auto;
  margin-top: 8px;
  font-size: 14px;
  padding-left: 10px;
}
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: auto;
}
.prevPrice {
  font-size: 12px;
  color: teal;
  text-decoration: line-through;
  text-decoration-color: black;
  opacity: 0.8;
}
.button {
  margin-top: 10px;
  margin-left: 10px;
  padding-left: 15px;
}
.sortSelect {
  display: flex;
  justify-content: space-around;
  color: antiquewhite;
  align-items: flex-end;
}
.pComponent {
  color: plum;
  font-size: 20px;
  padding: 200px;
}
.input {
  background-color: lightgray;
  border-top: transparent;
  border-left: transparent;
  border-right: transparent;
  border-bottom: 4px solid plum;
  padding: 8px 12px;
  font-size: 16px;
}
.select {
  height: 35px;
  width: 150px;
  background-color: lightgray;
  border: transparent;
  border-bottom: 4px solid plum;
}
@media (max-width: 450px) {
  .main {
    margin: 0;
    width: 48%;
    margin-left: 5px;
    margin-top: 10px;
  }
  .price {
    padding: 0;
  }
}
