.mainform {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 41%;
  height: 260px;
  background-color: white;
  border-radius: 15%;
  border: 4px solid teal;
}
.img {
  width: 194px;
  height: 185px;
  border-radius: 25%;
}
.prevPrice {
  color: teal;
  text-decoration: line-through;
  text-decoration-color: black;
  opacity: 0.8;
}
.price {
  color: rgb(56, 56, 56);
  width: auto;
  margin-top: 8px;
  margin-left: 28px;
}
.form {
  display: flex;
  align-items: center;
  width: 200px;
  height: 30px;
  margin-left: 40px;
}
.title {
  word-wrap: break-word;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(56, 56, 56);
}
.start {
  width: 500px;
  height: 300px;
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 100px;
  flex-direction: row-reverse;
  border-radius: 25px;
  border: 4px solid teal;
}
.form2 {
  color: antiquewhite;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;
}
.formTitle {
  color: teal;
  margin-top: 50px;
  font-size: 25px;
  margin-bottom: 90px;
}
.backButton {
  border: 4px solid teal;
  border-radius: 15px;
  background: white;
  font-size: 21px;
  color: teal;
  margin-top: 50px;
  width: 200px;
  margin-left: 45px;
  transition: color 0.3s ease;
  transition: border-color 0.3s ease;
}
.backButton:hover {
  color: plum;
  border: 4px solid plum;
}
.selectSize {
  text-decoration: none;
  padding-bottom: 25px;
  user-select: none;
  color: teal;
}
.selectSize a {
  text-decoration: none;
  font-size: 17px;
  color: plum;
  margin-left: 3px;
}
.selectSize a:focus {
  border-bottom: 2px solid teal;
}
@media (max-width: 450px) {
  .start {
    flex-wrap: wrap;
    height: 500px;
    margin: 0;
    width: 330px;
  }
  .backButton {
    margin-top: 30px;
    margin-bottom: 30px;
    width: 40%;
  }
  .formTitle {
    margin: 20px;
  }
  .form2 {
    margin-bottom: -80px;
  }
  .mainform {
    width: 65%;
  }
  .price {
    margin-left: 37px;
  }
}
